body {
    background-color: var(--body-bg-color);
    padding: 0 calc(12px - (100vw - 100%)) 0 0;
}

.app-wrapper {
    display: grid;
    width: 1280px;
    margin: 0 auto;
    grid-template-areas:
  "h h"
  "n c";
    overflow-x: hidden;

    grid-template-rows: 80px 1fr;
    grid-template-columns: 220px 960px;
    font-family: "Montserrat";
}

.app-wrapper-content {
    grid-area: c;
    margin-top: 20px;
    background: var(--body-bg-color);
    border-radius: 12px;
}


:root {
    --body-bg-color: #EDEEF0;
    --wrap-bg-color: white;
    --head-bg-color: white;
    --black-white-text-color: black;
    --white-black-text-color: white;
    --707070-EDEEF0-text-color: #707070;
    --9d9aa8-w-text-color: #9d9aa8;
    --color-block: #8683F8;
    --mininav-border-color: rgba(0, 0, 0, 1);
    --wrap-size: 1440px;
    --hover-block: #A183F8;
    --active-block: #9D9AF6;
    --unfollow-color: #EDEEF0;
}

[data-theme="dark"] {
    --body-bg-color: #18181A;
    --wrap-bg-color: #202022;
    --head-bg-color: #202022;
    --black-white-text-color: white;
    --white-black-text-color: black;
    --707070-EDEEF0-text-color: #707070;
    --707070-text-color: white;
    --9d9aa8-w-text-color: white;
    --mininav-border-color: #9d9aa8;
    --unfollow-color: #28282A;
}

::-webkit-scrollbar {
    width: 12px;
}

::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, 0.1); /* color of the tracking area */
    width: 0px;
}

::-webkit-scrollbar-thumb {
    background-color: var(--707070-EDEEF0-text-color); /* color of the scroll thumb */
    border-radius: 20px; /* roundness of the scroll thumb */ /* creates padding around scroll thumb */
}


